#workHistory {
  background-color: #fbfefb;
  padding: 10px;
  padding-top: 0;
  font-style: italic;
}
#companyList {
  padding: 0 60px;
  list-style: none;
  display: grid;
  row-gap: 20px;
  column-gap: 60px;
  grid-template-columns: 1fr 1fr 1fr;
}
.companyItem:hover {
  background-color: rgba(131, 197, 190, 0.5);
}
.workTime {
  text-align: center;
}

.companyItem {
  background-color: rgba(131, 197, 190, 0.9);
  margin: 30px;
  cursor: pointer;
  padding: 10px;
  border-radius: 15px;
  border: 1px solid black;
  box-shadow: 0 0 5px 2px rgb(14, 14, 14);
}
h2 {
  text-align: center;
  padding-top: 20px;
}
ul {
}

.companyName {
  max-width: 100px;
  max-height: 100px;
  text-align: center;
  margin: auto;
}

@media screen and (max-width: 992px) {
  #companyList {
    gap: 20px;
  }
  .companyItem {
    margin: 10px;
  }
}
@media screen and (max-width: 600px) {
  #companyList {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    padding: 0 20px;
  }
  .companyItem {
    margin: 0;
  }
}
