#textContainer h2 {
  margin: 0;
  text-align: center;
  font-size: xx-large;
}
.wrap {
  background-image: url("./myPicture.jpeg");
  height: 70vh;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
}
#textContainer p {
  text-align: justify;
  margin: 20px 80px;
}
#textContainer {
  display: flex;
  flex-direction: column-reverse;
  position: absolute;
  /* z-index: 100; */
  margin: auto;
  height: inherit;
  line-height: 1.5;
  background-color: rgba(255, 255, 255, 0.4);
}

@media screen and (max-width: 992px) {
  .wrap {
    height: 90vh;
  }
  #about section {
    flex-direction: row;
  }
  #textContainer {
    margin: auto;
    line-height: 1.5;
  }
}
@media screen and (max-width: 600px) {
  .wrap {
    height: 90vh;
  }
  #about section {
    flex-direction: column;
  }
  #textContainer {
    padding: inherit;
  }
  #textContainer p {
    text-align: justify;
    margin: 20px;
  }
}
