nav {
  background-color: rgba(242, 59, 56, 0.9);
  display: flex;
  justify-content: space-between;
}

.logo {
  max-width: 80px;
  max-height: 80px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  margin: auto 10px;
}
a {
  text-decoration: none;
}
#LargeScreen li a {
  color: black;
  text-decoration: none;
  font-family: sans-serif system-ui;
  font-size: large;
  font-weight: 600;
}
#LargeScreen,
#smallScreen {
  padding-left: 25px;
  padding-right: 100px;
}
#LargeScreen ul {
  display: flex;
  flex-direction: row;
}
#navOnLargeScreen div {
  background-color: aliceblue;
  cursor: pointer;
  padding: 5px;
  color: black;
  border: 1px solid black;
  border-radius: 10px;
  text-decoration: none;
  font-family: sans-serif system-ui;
  font-size: large;
  font-weight: 500;
}
#smallScreen {
  display: none;
}

#navOnLargeScreen #navList {
  display: none;
  line-height: 2;
}

#navOnLargeScreen ul {
  position: absolute;
  z-index: 1;
  display: flex;
  flex-direction: column;
  background-color: rgba(242, 59, 56, 0.9);
}

@media screen and (max-width: 992px) {
  #LargeScreen {
    padding-right: 25px;
  }
}

@media screen and (max-width: 660px) {
  #LargeScreen {
    display: none;
  }
  #dropDownMenu {
    margin: auto 40px;
  }
  #smallScreen {
    display: flex;
    padding-right: 25px;
  }
  #navOnSmallScreen {
    z-index: 1;
    background-color: rgba(242, 59, 56, 0.9);
    position: absolute;
    margin: 10px;
    right: 0;
    top: 46px;
    display: block;
    /* border: 1px solid black; */
    line-height: 1.5;
  }
  #navOnSmallScreen {
    display: none;
  }
  #navOnSmallScreen li a {
    color: whitesmoke;
    font-family: sans-serif system-ui;
  }
  #navOnSmallScreen li:hover {
    transition: 0.4s;
    background-color: rgba(69, 143, 135, 0.2);
  }
}
